<template>
  <div>
    <nav aria-label="Page navigation example">
        <ul class="pagination justify-content-center">
        <li class="page-item" :class="{disabled: !paginationData['has_pre']}"
        @click.prevent="getPage(paginationData['current_page']-1)">
            <a class="page-link" href="#" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
            </a>
        </li>
        <li  class="page-item" v-for="item in paginationData['total_pages']"
        :key="item.index"  @click.prevent="getPage(item)">
            <a class="page-link" href="#">
            {{item}}
            </a>
        </li>
        <li class="page-item" :class="{disabled: !paginationData['has_next']}" >
            <a class="page-link" href="#" aria-label="Next"
            @click.prevent="getPage(paginationData['current_page']+1)">
            <span aria-hidden="true">&raquo;</span>
            </a>
        </li>
        </ul>
    </nav>
  </div>
</template>

<script>
export default {
  props: ['paginationData'],
  methods: {
    getPage(item) {
      // console.log(item);
      this.$emit('get', item);
    },
  },
};
</script>
