<template>
<div class="container">
  <div class="position-fixed d-block d-flex justify-content-end"
  style="right: 5%; width: 40px">
    <Toast :text="text" ref="toast"/>
    <ToastFail :text="text" ref="toastFail"/>
  </div>
  <div class="row">
    <div class="col-3  mt-4">
      <a href="cart.html" class="btn btn-primary">購物車</a>
    </div>
    <div class="col-3 offset-6  mt-4">
      <!-- <button class="btn btn-primary" @click="openModal('productModal','new',item)"> -->
      <button class="btn btn-primary" type="button" @click="openModal(true)">

        建立新的產品
      </button>
    </div>
  </div>
  <table class="table mt-4">
  <thead>
    <tr>
      <th width="120">
        分類
      </th>
      <th width="120"> 產品名稱</th>
      <th width="120">
        原價
      </th>
      <th width="120">
        售價
      </th>
      <th width="100">
        是否啟用
      </th>
      <th width="120">
        編輯
      </th>
    </tr>
  </thead>
  <tbody v-for="(item,id) in products" :key="id">
    <tr>
      <td>{{item.category}}</td>
      <td>{{item.title}}</td>
      <td class="text-end"> {{item.origin_price}} </td>
      <td class="text-end"> {{item.price}} </td>
      <td>
        <span class="text-success"  v-if="item['is_enabled']"> 啟用</span>
        <span v-else> 未啟用</span>
      </td>
      <td>
        <div class="btn-group">
          <button type="button" class="btn btn-outline-primary btn-sm"
          @click="openModal(false, item)">
            編輯
          </button>
          <button type="button" class="btn btn-outline-danger btn-sm"
          @click="opendelModal(item)">
            刪除
          </button>
        </div>
      </td>
    </tr>
  </tbody>
</table>
<Page :pagination-data="pagination" @get="getAdminProducts"></Page>

 <ProductModal2
      @update-product="updateProduct"
      :product="tempProduct"
      :isNew="isNew"
      ref="productModal2"
    />
  <!-- <product-modal :temp="temp" @get-products="getProducts"></product-modal> -->
  <DeleteModal
    @del-item="deleteItem"
    :item="tempProduct"
    ref="deleteModal"
  />
</div>
</template>

<script>

// import { Modal } from 'bootstrap';
import Page from '@/components/Page.vue';
// import ProductModal from '@/components/ProductModal.vue';

import Toast from '@/components/Toast.vue';
import ToastFail from '@/components/ToastFail.vue';
import ProductModal2 from '@/components/ProductModal2.vue';
import DeleteModal from '@/components/DeleteModal.vue';

export default {
  components: {
    // Page, ProductModal,
    Page, ProductModal2, DeleteModal, Toast, ToastFail,
  },
  data() {
    return {
      products: [],
      pagination: {},
      isNew: false,
      tempProduct: {},
      temp: '',
      text: '',
    };
  },
  inject: ['emitter'],
  methods: {
    getAdminProducts(page = 1) {
      console.log('gogo', page);
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/products/?page=${page}`;
      this.$http.get(url)
        .then((res) => {
          console.log('107', res);
          console.log(res.data.products);
          this.products = res.data.products;
          this.pagination = res.data.pagination;
        })
        .catch((rej) => {
          console.log('getproducts error');
          console.log(rej.response);
        });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = { ...item };
        this.isNew = false;
      }
      const productComponent2 = this.$refs.productModal2;

      productComponent2.openModal();
    },
    opendelModal(item) {
      this.tempProduct = { ...item };
      const deleteComponent = this.$refs.deleteModal;
      deleteComponent.openModal();
    },
    updateProduct(item) {
      this.tempProduct = item;
      let method = 'post'; // 預設方法為新增
      let methodUrl = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product`; // 新增的url
      this.text = '成功新增';
      if (!this.isNew) { // 判斷是編輯資料
        const { id } = this.tempProduct; // 取出資料id
        method = 'put'; // 將方法修改成編輯api的put
        console.log(id, this.tempProduct);
        methodUrl = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`; // 編輯的 url
        this.text = '成功更新';
      }
      const productComponent2 = this.$refs.productModal2;
      this.$http[method](methodUrl, { data: this.tempProduct }) // 依照新增或編輯帶入相對應 方法與url
        .then((res) => {
          console.log(res);
          productComponent2.hideModal();
          this.getAdminProducts(); // 更新畫面
          this.toastComponent.openToast();
          this.toastComponent.autoHideToast();
          // alert(res.data.message);
        })
        .catch((rej) => {
          console.log(rej.response);
          productComponent2.hideModal();
        });
    },
    deleteItem() {
      console.log('delete', this.tempProduct);
      const methodUrl = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`;
      const deleteComponent = this.$refs.deleteModal;
      this.$http.delete(methodUrl, { data: this.tempProduct }) // 依照新增或編輯帶入相對應 方法與url
        .then((res) => {
          this.text = '成功刪除';
          console.log(res);
          deleteComponent.hideModal();
          this.getAdminProducts(); // 更新畫面
          this.toastComponent.openToast();
          this.toastComponent.autoHideToast();
          // alert(res.data.message);
        })
        .catch((rej) => {
          console.log(rej.response);
          deleteComponent.hideModal();
        });
    },
  },
  mounted() {
    this.getAdminProducts();
    this.toastComponent = this.$refs.toast;
    this.toastFailComponent = this.$refs.toastFail;
  },
};
</script>
